import React from "react";

export const AuthInput = (props) => {
  const {
    handleChange,
    label,
    important,
    type,
    fieldType,
    placeholder,
  } = props;

  return (
    <div className="auth-string">
      <div className="auth-label">
        <span>{label} </span>
        {important && <span>*</span>}
      </div>
      <div className="auth-input">
        <input
          type={type || "text"}
          onChange={(e) => handleChange(e, fieldType)}
          required={important}
          placeholder={placeholder}
        />
      </div>
    </div>
  );
};
