import { inject } from "mobx-react";
import { useHistory } from "react-router-dom";
import React, { useEffect, useState } from "react";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import loader from "@images/loader.gif";
import image from "@images/logo-auth.svg";

import "./styles.css";

const fieldData = [
  {
    label: "Имя",
    fieldType: "name",
    placeholder: "Антон",
  },
  {
    label: "Название компании",
    fieldType: "company",
    placeholder: "Яндекс",
  },
  {
    label: "Номер телефона",
    fieldType: "phone",
    placeholder: "+7 123 456 789",
  },
];

export const RegistrationComplete = inject("authStore")((props) => {
  const { completeRegistration } = props.authStore;
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const [name, setName] = useState("");
  const [company, setCompany] = useState("");
  const [phone, setPhone] = useState("");
  const [error, setError] = useState(false);
  const [errMesage, setErrMessage] = useState("");

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (!id) {
      history.push("registration");
    }
  });

  const handleClick = async () => {
    try {
      setErrMessage("");
      if (!name || !company || !phone) {
        setErrMessage("Заполните все поля");
        return;
      }
      setLoading(true);
      const id = localStorage.getItem("id");
      await completeRegistration(id, {
        name,
        company,
        phoneNumber: phone,
      });
      setLoading(false);

      history.push("/deals");
    } catch (e) {
      setLoading(false);
      setErrMessage("Ошибка сервера, попробуйте еще раз");
      setError(true);
    }
  };

  const handleChange = (event, fieldType) => {
    const { value } = event.target;

    switch (fieldType) {
      case "name":
        setName(value);
        break;
      case "company":
        setCompany(value);
        break;
      case "phone":
        setPhone(value);
        break;
      default:
        break;
    }
  };

  const AuthInputs = fieldData.map(({ label, fieldType, placeholder }) => (
    <AuthInput
      label={label}
      important={true}
      fieldType={fieldType}
      handleChange={handleChange}
      placeholder={placeholder}
    ></AuthInput>
  ));

  return (
    <div className="auth">
      <div className="auth-modal">
        <div className="auth-logo">
          <img src={image} />
        </div>
        <div className="auth-container">
          <div className="auth-title">
            <span>Регистрация</span>
          </div>
          <div className="auth-row">{AuthInputs}</div>
          <div className="auth-row">
            <div className="auth-string">
              <div className="auth-button">
                <AuthButton handleClick={handleClick} loading={loading} value="Регистрация"></AuthButton>
              </div>
            </div>
          </div>
          {error && (
            <div className="auth-row">
              <div className="auth-string">
                <div className="auth-error">
                  <span>
                    {errMesage || "Неверный пароль"}
                  </span>
                </div>
              </div>
            </div>
          )}
        </div>
      </div>
    </div>
  );
});
