import React from "react";

import loader from '@images/loader.gif';

export const AuthButton = props => {
    const { loading, handleClick, value } = props;

    return <>
        {
            loading 
            ?
            <center>
              <img width="30px" src={loader} alt=""/>
            </center>
            :
            <input
              type="submit"
              className="button"
              onClick={handleClick}
              value="Войти"
            />
          }
    </>
}
