import { inject } from "mobx-react";
import React, { useEffect, useState } from "react";
import { Link, useHistory } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import logoImage from "@images/logo-auth.svg";

const fieldData = [
  {
    label: "E-mail",
    fieldType: "email",
    placeholder: "example@mail.ru",
  },
  {
    label: "Пароль",
    fieldType: "password",
    placeholder: "*****",
    type: "password",
  },
];

export const RegistrationMain = inject("authStore")((props) => {
  const checkEmail = /[a-zA-Z0-9]+@[a-zA-Z]+\.[a-z]+/;

  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const { registrateUser } = props.authStore;
  const [error, setError] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [errMessage, setErrMessage] = useState("");

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (id) {
      history.push("/registration/complete");
    }
  });

  const handleClick = async () => {
    try {
      setErrMessage("");
      if (!checkEmail.test(email)) {
        setErrMessage("Введите правильную почту");
        setError(true);
        return;
      }
      setLoading(true);
      const id = await registrateUser({ email, password });
      setLoading(false);
      localStorage.setItem("id", id);
      history.push("/registration/complete");
    } catch (e) {
      setLoading(false);
      setError(true);
      if (e.message === "User exists!") {
        setErrMessage("Почту уже существует");
      }
    }
  };

  const handleChange = (event, fieldType) => {
    const { value } = event.target;

    switch (fieldType) {
      case "email":
        setEmail(value);
        break;
      case "password":
        setPassword(value);
        break;
      default:
        return;
    }
  };

  const AuthInputs = fieldData.map(
    ({ label, fieldType, placeholder, type }) => (
      <AuthInput
        type={type}
        label={label}
        important={true}
        fieldType={fieldType}
        placeholder={placeholder}
        handleChange={handleChange}
      ></AuthInput>
    )
  );

  return (
    <div className="auth">
      <div className="auth-modal">
        <div className="auth-logo">
          <img src={logoImage} />
        </div>
        <div className="auth-container">
          <div className="auth-title">
            <span>Регистрация</span>
          </div>
          <div className="auth-row">{AuthInputs}</div>
          <div className="auth-row">
            <div className="auth-string">
              <div className="auth-button">
                <AuthButton handleClick={handleClick} loading={loading} value="Регистрация"></AuthButton>
              </div>
            </div>
          </div>
          {error && (
            <div className="auth-row">
              <div className="auth-string">
                <div className="auth-error">
                  <span>
                    {errMessage || "Неверный пароль"}
                  </span>
                </div>
              </div>
            </div>
          )}
        </div>
        <div className="auth-footer">
          <div className="auth-items">
            <div className="auth-item">
              <Link to="/login" className="auth-link">
                <span>Войти</span>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
