import { inject } from "mobx-react";
import React, { useState, useEffect } from "react";
import { Link, useHistory } from "react-router-dom";

import { AuthInput } from "@shared/AuthInput";
import { AuthButton } from "@shared/AuthButton";

import loader from "@images/loader.gif";
import image from "@images/logo-auth.svg";


const fieldData = [
  {
    label: "E-mail",
    fieldType: "email",
    placeholder: "example@mail.ru",
  },
  {
    label: "Пароль",
    fieldType: "password",
    type: "password",
    placeholder: "*****",
  },
];

export const Login = inject("authStore")((props) => {
  const checkEmail = /[a-zA-Z0-9]+@[a-zA-Z]+\.[a-z]+/;

  const { authenticateUser } = props.authStore;
  const history = useHistory();

  const [loading, setLoading] = useState(false);
  const [error, setError] = useState(false);
  const [errMessage, setErrMessage] = useState("");
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");

  useEffect(() => {
    const id = localStorage.getItem("id");

    if (id) {
      history.push("/registration");
    }
  });

  const handleClick = async () => {
    try {
      setErrMessage("");
      if (!checkEmail.test(email)) {
        setErrMessage("Введите правильную почту");
        setError(true);
        return;
      }
      setLoading(true);
      await authenticateUser({ email, password });
      // alert("hi");
      setLoading(false);
      history.push("/deals");
    } catch (e) {
      setLoading(false);
      setError(true);
    }
  };

  const handleChange = (event, fieldType) => {
    const { value } = event.target;
    switch (fieldType) {
      case "email":
        setEmail(value);
        break;
      case "password":
        setPassword(value);
        break;
      default:
        break;
    }
  };

  const AuthInputs = fieldData.map(
    ({ label, fieldType, type, placeholder }) => (
      <AuthInput
        label={label}
        fieldType={fieldType}
        handleChange={handleChange}
        important={true}
        type={type}
        placeholder={placeholder}
      ></AuthInput>
    )
  );

  return (
    <div className="auth">
      <div className="auth-modal">
        <div className="auth-logo">
          <img src={image} />
        </div>
        <div className="auth-container">
          <div className="auth-title">
            <span>Войти</span>
          </div>
          <div className="auth-row">{AuthInputs}</div>
          <div className="auth-row">
            <div className="auth-string">
              <div className="auth-button">
                <AuthButton handleClick={handleClick} loading={loading} value="Войти"></AuthButton>
              </div>
            </div>
          </div>
          {error && (
            <div className="auth-row">
              <div className="auth-string">
                <div className="auth-error">
                  <span>{errMessage || "Неверный пароль"}</span>
                </div>
              </div>
            </div>
          )}
        </div>
        <div className="auth-footer">
          <div className="auth-items">
            <div className="auth-item">
              <Link to="/password-recovery" className="auth-link">
                <span>Забыли пароль?</span>
              </Link>
            </div>
            <div className="auth-item">
              <Link to="/registration" className="auth-link">
                <span>Регистрация</span>
              </Link>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
});
